.pub-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.pub-title {
    font-size: 1.5vw;
}

.pub-authors {
    font-size: 1vw;
}

.pub-conference {
    font-style: italic;
}

.pub-author-conf {
    font-size: 1vw;
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.pub-a:link {
    color: rgb(252, 238, 238);
  }
  
/* visited link */
.pub-a:visited {
    color: rgb(252, 238, 238);
}
  
/* mouse over link */
.pub-a:hover {
    color: #C96567;
}
  
/* selected link */
a:active {
  color: black;
}