body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pub-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.pub-title {
    font-size: 1.5vw;
}

.pub-authors {
    font-size: 1vw;
}

.pub-conference {
    font-style: italic;
}

.pub-author-conf {
    font-size: 1vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: baseline;
            align-items: baseline;
}

.pub-a:link {
    color: rgb(252, 238, 238);
  }
  
/* visited link */
.pub-a:visited {
    color: rgb(252, 238, 238);
}
  
/* mouse over link */
.pub-a:hover {
    color: #C96567;
}
  
/* selected link */
a:active {
  color: black;
}
.Self-Block {
    padding: 10px;
    margin: 30px;
    max-width: 55%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: left;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.Intro {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 10px;
    -webkit-align-items: center;
            align-items: center;
}

.Subsequent-Text {
    padding: 1px;
    margin: 1px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.useless {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 10px;
}

.face-img {
    max-width:80%;
    height: auto;
    margin: 10px;
}

.face {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.face-text {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 1px;
    margin: 1px;
    max-width: 50%;
}

@media all and (max-width: 1100px) {

    .Self-Block{
        -webkit-align-items: center;
                align-items: center;
        max-width: 100%;
    }

    .face-text{
        max-width: 100%;
    }

    .face-img{
        max-width: 50%;
    }

    .Intro{
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-flex-direction: column;
              flex-direction: column;
    }
  }
.header {
  background-color: #C96567;
  min-height: 5vh;
  padding: 7px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #314455;
}

.header-name {
    margin: 10px;
    padding-left: 10px;
    font-size: 2vw;
}

.header-link-divider {
    margin: 5px;
}

.header-links {
    font-size: 1.5vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    margin: 3px;
}

.header-link {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 5px;
}

.head-a:link {
    color: #314455;
  }
  
/* visited link */
.head-a:visited {
  color: #314455;
}
  
/* mouse over link */
.head-a:hover {
  color: rgb(252, 238, 238);
}
  
/* selected link */
.head-a:active {
  color: black;
}

@media all and (max-width: 500px) {
  .header{
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .header-links{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
.App {
  text-align: center;
  background-color: #314455;
}

.main-content {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  margin: 5px;
  background-color: #314455;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: rgb(252, 238, 238);
}

.App-link {
  color: #61dafb;
}



.footer {
    left: 0;
    bottom: 0;
    padding: 10px;
    width: 100%;
    background-color: #97AABD;
    color: #314455;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

.footer-link {
    padding: 10px;
}

.foot-a:link {
    color: #314455;
  }
  
  /* visited link */
.foot-a:visited {
    color: #314455;
}
  
.foot-a:hover {
    color: rgb(252, 238, 238);
}
  
.foot-a:active {
    color: black;
}
